:root {
  --color-primary: #10b981;
  --color-excellent: #10b981;
  --color-good: #34d399;
  --color-neutral: #fbbf24;
  --color-bad: #fb923c;
  --color-terrible: #ef4444;
  --color-bg: #f0fdf4;
  --color-border: #e5e7eb;
  --color-text: #111827;
  --color-text-muted: #6b7280;
  --space-sm: .75rem;
  --space-md: 1rem;
  --space-lg: 1.5rem;
  --space-xl: 2rem;
  --radius: .75rem;
  --transition: .2s ease;
}

body {
  color: var(--color-text);
  background: var(--color-bg);
  min-width: 320px;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
  line-height: 1.6;
}

.tab-input, .screen {
  display: none;
}

#tab-diary:checked ~ .app-container .screen--diary, #tab-history:checked ~ .app-container .screen--history, #tab-notes:checked ~ .app-container .screen--notes, #tab-calendar:checked ~ .app-container .screen--calendar {
  display: block;
}

#tab-diary:checked ~ .app-container .app-navigation label[for="tab-diary"], #tab-history:checked ~ .app-container .app-navigation label[for="tab-history"], #tab-notes:checked ~ .app-container .app-navigation label[for="tab-notes"], #tab-calendar:checked ~ .app-container .app-navigation label[for="tab-calendar"] {
  background: var(--color-primary);
  color: #fff;
  border-color: var(--color-primary);
}

.app-container {
  max-width: 56rem;
  padding: var(--space-xl) var(--space-lg);
  flex-direction: column;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
}

.main-content {
  gap: var(--space-xl);
  flex-direction: column;
  flex: 1;
  display: flex;
}

.app-header {
  text-align: center;
  margin-bottom: var(--space-xl);
}

.app-title {
  color: var(--color-primary);
  margin-bottom: var(--space-sm);
  font-size: 2.5rem;
  font-weight: 700;
}

.app-subtitle {
  color: var(--color-text-muted);
  margin-bottom: var(--space-lg);
  font-size: 1.125rem;
}

.app-navigation {
  gap: var(--space-sm);
  margin-top: var(--space-lg);
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.nav-button {
  padding: var(--space-md) var(--space-lg);
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
  color: var(--color-text);
  background: #fff;
  align-items: center;
  gap: .5rem;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
}

.nav-button:hover {
  border-color: var(--color-primary);
  color: var(--color-primary);
}

.nav-icon {
  font-size: 1.25rem;
}

.nav-label {
  font-size: .9375rem;
}

.section-title {
  margin-bottom: var(--space-xl);
  text-align: center;
  font-size: 1.5rem;
  font-weight: 600;
}

.mood-grid {
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
  grid-template-columns: repeat(5, 1fr);
  display: grid;
}

.mood-card {
  background: var(--color-primary);
  border-radius: var(--radius);
  padding: var(--space-lg);
  cursor: pointer;
  align-items: center;
  gap: var(--space-sm);
  color: #fff;
  transition: var(--transition);
  border: none;
  flex-direction: column;
  display: flex;
}

.mood-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 6px #0000001a;
}

.mood-card.selected {
  outline: 4px solid #10b9814d;
}

.mood-card__emoji {
  font-size: 2.5rem;
}

.mood-card__label {
  font-size: .875rem;
  font-weight: 600;
}

.mood-card--excellent {
  background: var(--color-excellent);
}

.mood-card--good {
  background: var(--color-good);
}

.mood-card--neutral {
  background: var(--color-neutral);
}

.mood-card--bad {
  background: var(--color-bad);
}

.mood-card--terrible {
  background: var(--color-terrible);
}

.note-form, .diary-form {
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  padding: var(--space-lg);
  background: #fff;
}

.note-form {
  gap: var(--space-md);
  margin-top: var(--space-lg);
  flex-direction: column;
  display: flex !important;
}

.diary-form {
  margin-bottom: var(--space-xl);
}

.mood-selector-section .submit-button {
  margin-top: var(--space-lg);
}

.form-group {
  margin-top: var(--space-md);
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.form-label {
  color: var(--color-text-muted);
  font-size: .875rem;
  font-weight: 500;
}

.form-textarea, .form-input, .form-select {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  font-family: inherit;
  font-size: 1rem;
}

.form-textarea {
  resize: vertical;
}

.form-textarea:focus, .form-input:focus, .form-select:focus {
  border-color: var(--color-primary);
  outline: none;
}

.submit-button {
  width: 100%;
  padding: var(--space-md);
  background: var(--color-primary);
  color: #fff;
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
  border: none;
  font-size: 1rem;
  font-weight: 600;
}

.submit-button:hover {
  background: #059669;
}

.diary-mood-selector {
  gap: var(--space-sm);
  margin-bottom: var(--space-lg);
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.diary-mood-btn {
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  cursor: pointer;
  width: 60px;
  height: 60px;
  transition: var(--transition);
  background: #fff;
  font-size: 1.5rem;
}

.diary-mood-btn:hover {
  border-color: var(--color-primary);
  transform: scale(1.1);
}

.diary-mood-btn.selected {
  background: var(--color-primary);
  border-color: var(--color-primary);
}

.mood-history-section, .diary-section {
  width: 100%;
}

.mood-history-list, .diary-list, #history-list, #notes-list {
  gap: var(--space-lg);
  flex-direction: column;
  display: flex;
}

.history-item, .mood-entry {
  gap: var(--space-md);
  padding: var(--space-lg);
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  transition: var(--transition);
  background: #fff;
  display: flex;
  position: relative;
}

.history-item:hover, .mood-entry:hover {
  border-color: var(--color-primary);
  box-shadow: 0 2px 4px #0000000d;
}

.mood-entry__emoji {
  flex-shrink: 0;
  font-size: 2rem;
}

.mood-entry__content {
  flex: 1;
  padding-right: 2.5rem;
}

.mood-entry__header {
  flex-direction: column;
  align-items: flex-start;
  gap: .25rem;
  margin-bottom: .5rem;
  display: flex;
}

.mood-entry__title {
  font-size: 1.125rem;
  font-weight: 600;
}

.mood-entry__date {
  color: var(--color-text-muted);
  font-size: .875rem;
}

.mood-entry__note {
  color: var(--color-text-muted);
  font-size: .9375rem;
  line-height: 1.7;
}

.mood-entry__delete {
  top: var(--space-lg);
  right: var(--space-lg);
  cursor: pointer;
  opacity: 0;
  transition: var(--transition);
  color: var(--color-text-muted);
  background: none;
  border: none;
  padding: .25rem .5rem;
  font-family: Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1;
  position: absolute;
  transform: none;
}

.mood-entry:hover .mood-entry__delete {
  opacity: 1;
}

.mood-entry__delete:hover {
  color: var(--color-terrible);
  transform: scale(1.1);
}

.diary-divider, .section-divider {
  background: var(--color-border);
  height: 2px;
  margin: var(--space-xl) 0;
  border: none;
}

.diary-list-title {
  margin-bottom: var(--space-lg);
  font-size: 1.25rem;
  font-weight: 600;
}

.empty-state {
  text-align: center;
  padding: var(--space-xl);
}

.empty-state.hidden, .hidden {
  display: none;
}

.empty-state__icon {
  opacity: .5;
  margin-bottom: var(--space-md);
  font-size: 4rem;
}

.empty-state__message {
  color: var(--color-text-muted);
  font-size: 1.125rem;
}

.calendar-controls {
  margin-bottom: var(--space-lg);
  justify-content: space-between;
  align-items: center;
  gap: var(--space-md);
  display: flex;
}

.calendar-month {
  text-align: center;
  flex: 1;
  font-size: 1.25rem;
  font-weight: 600;
}

.calendar-nav-btn {
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  cursor: pointer;
  width: 40px;
  height: 40px;
  color: var(--color-text-muted);
  transition: var(--transition);
  background: #fff;
  font-size: 1rem;
}

.calendar-nav-btn:hover {
  border-color: var(--color-primary);
  background: var(--color-primary);
  color: #fff;
}

.calendar-grid {
  margin-bottom: var(--space-md);
  grid-template-columns: repeat(7, 1fr);
  gap: .5rem;
  display: grid;
}

.calendar-weekdays {
  grid-template-columns: repeat(7, 1fr);
  gap: .5rem;
  margin-bottom: .5rem;
  display: grid;
}

.calendar-weekday {
  text-align: center;
  color: var(--color-text-muted);
  padding: var(--space-sm);
  font-size: .875rem;
  font-weight: 600;
}

.calendar-day {
  aspect-ratio: 1;
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  cursor: pointer;
  min-width: 0;
  min-height: 50px;
  max-height: 100px;
  transition: var(--transition);
  background: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: .25rem;
  display: flex;
  overflow: hidden;
}

.calendar-day:hover {
  border-color: var(--color-primary);
  transform: scale(1.05);
}

.calendar-day--other-month {
  opacity: .4;
  pointer-events: none;
  background: #f9fafb;
}

.calendar-day--other-month .calendar-day__number {
  color: var(--color-text-muted);
  font-weight: 400;
}

.calendar-day--today {
  border-color: var(--color-primary);
  border-width: 3px;
}

.calendar-day--today:hover {
  background: #10b9811a;
}

.calendar-day--has-mood {
  background: var(--color-bg);
}

.calendar-day--has-mood:hover {
  background: #10b98133;
}

.calendar-day__number {
  margin-bottom: .25rem;
  font-size: .75rem;
  line-height: 1;
}

.calendar-day__emoji {
  font-size: 1.25rem;
  line-height: 1;
}

.calendar-legend {
  gap: var(--space-md);
  margin-top: var(--space-lg);
  padding-top: var(--space-lg);
  border-top: 1px solid var(--color-border);
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.calendar-legend__item {
  color: var(--color-text-muted);
  align-items: center;
  gap: .5rem;
  font-size: .875rem;
  display: flex;
}

.calendar-legend__emoji {
  font-size: 1.25rem;
}

.app-footer {
  margin-top: var(--space-xl);
  padding-top: var(--space-xl);
  text-align: center;
  border-top: 1px solid var(--color-border);
}

.footer-text {
  color: var(--color-text-muted);
  font-size: .875rem;
}

.toast-notification {
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-lg);
  border-radius: var(--radius);
  z-index: 3000;
  opacity: 0;
  border-left: 4px solid var(--color-primary);
  background: #fff;
  min-width: 320px;
  max-width: 400px;
  transition: all .4s cubic-bezier(.68, -.55, .265, 1.55);
  display: flex;
  position: fixed;
  top: 24px;
  right: 24px;
  transform: translateX(450px);
  box-shadow: 0 10px 40px #00000026, 0 0 0 1px #0000000d;
}

.toast-notification--show {
  opacity: 1;
  transform: translateX(0);
}

.toast-notification__icon {
  flex-shrink: 0;
  font-size: 2rem;
  animation: .5s scaleIn;
}

@keyframes scaleIn {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.toast-notification__content {
  flex: 1;
}

.toast-notification__title {
  color: var(--color-text);
  margin-bottom: .25rem;
  font-size: 1rem;
  font-weight: 600;
}

.toast-notification__message {
  color: var(--color-text-muted);
  font-size: .875rem;
}

@media (width <= 768px) {
  .app-title {
    font-size: 2rem;
  }

  .mood-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width <= 480px) {
  .app-container {
    padding: var(--space-md);
  }

  .app-title {
    font-size: 1.875rem;
  }

  .nav-label {
    display: none;
  }

  .nav-button {
    padding: .75rem;
  }

  .mood-grid {
    gap: var(--space-sm);
    grid-template-columns: repeat(2, 1fr);
  }

  .mood-card {
    padding: var(--space-sm);
  }

  .mood-card__emoji {
    font-size: 2rem;
  }

  .mood-card__label {
    font-size: .75rem;
  }

  .mood-entry__header {
    flex-direction: column;
    align-items: flex-start;
  }

  .calendar-grid {
    gap: .25rem;
  }

  .calendar-day {
    min-height: 40px;
    max-height: 60px;
    padding: .125rem;
    font-size: .875rem;
  }

  .calendar-day__number {
    margin-bottom: .125rem;
    font-size: .625rem;
  }

  .calendar-day__emoji {
    font-size: .875rem;
  }

  .calendar-weekday {
    padding: .25rem;
    font-size: .7rem;
  }

  .calendar-month {
    font-size: .95rem;
  }

  .calendar-nav-btn {
    width: 36px;
    height: 36px;
    font-size: .875rem;
  }

  .diary-mood-btn {
    width: 50px;
    height: 50px;
    font-size: 1.25rem;
  }

  .toast-notification {
    min-width: auto;
    max-width: none;
    padding: var(--space-md);
    top: 16px;
    left: 16px;
    right: 16px;
  }

  .toast-notification__icon {
    font-size: 1.5rem;
  }

  .toast-notification__title {
    font-size: .9375rem;
  }

  .toast-notification__message {
    font-size: .8125rem;
  }
}

@media (width <= 380px) {
  .calendar-grid {
    gap: .125rem;
  }

  .calendar-day {
    border-width: 1px;
    min-height: 35px;
    max-height: 50px;
    padding: .0625rem;
  }

  .calendar-day--today {
    border-width: 2px;
  }

  .calendar-day__number {
    margin-bottom: .0625rem;
    font-size: .5rem;
  }

  .calendar-day__emoji {
    font-size: .75rem;
  }

  .calendar-weekday {
    padding: .125rem;
    font-size: .625rem;
  }

  .calendar-nav-btn {
    width: 32px;
    height: 32px;
    font-size: .75rem;
  }

  .calendar-month {
    font-size: .875rem;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
    animation: none !important;
  }
}

.settings-button {
  top: var(--space-lg);
  right: var(--space-lg);
  border: 2px solid var(--color-border);
  cursor: pointer;
  width: 3rem;
  height: 3rem;
  transition: var(--transition);
  background: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  display: flex;
  position: absolute;
}

.settings-button:hover {
  border-color: var(--color-primary);
  transform: rotate(90deg);
}

.modal {
  z-index: 2000;
  width: 100%;
  height: 100%;
  padding: var(--space-lg);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.hidden {
  display: none;
}

.modal__overlay {
  backdrop-filter: blur(4px);
  background: #00000080;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.modal__content {
  border-radius: var(--radius);
  background: #fff;
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  animation: .3s modalFadeIn;
  position: relative;
  overflow-y: auto;
  box-shadow: 0 20px 60px #0000004d;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: scale(.9)translateY(20px);
  }

  to {
    opacity: 1;
    transform: scale(1)translateY(0);
  }
}

.modal__header {
  padding: var(--space-lg);
  border-bottom: 1px solid var(--color-border);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal__title {
  color: var(--color-text);
  font-size: 1.5rem;
  font-weight: 600;
}

.modal__close {
  color: var(--color-text-muted);
  cursor: pointer;
  width: 2rem;
  height: 2rem;
  transition: var(--transition);
  background: none;
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 2rem;
  display: flex;
}

.modal__close:hover {
  background: var(--color-bg);
  color: var(--color-text);
}

.modal__body {
  padding: var(--space-lg);
}

.settings-section {
  margin-bottom: var(--space-xl);
}

.settings-section:last-child {
  margin-bottom: 0;
}

.settings-section__title {
  color: var(--color-text);
  margin-bottom: var(--space-md);
  font-size: 1.125rem;
  font-weight: 600;
}

.settings-item {
  margin-bottom: var(--space-md);
}

.settings-toggle {
  align-items: center;
  gap: var(--space-md);
  cursor: pointer;
  padding: var(--space-md);
  background: var(--color-bg);
  border-radius: var(--radius);
  border: 2px solid var(--color-border);
  transition: var(--transition);
  flex-direction: row;
  display: flex;
}

.settings-toggle:hover {
  border-color: var(--color-primary);
  background: #10b9810d;
}

.settings-toggle input[type="checkbox"] {
  cursor: pointer;
  width: 1.5rem;
  height: 1.5rem;
  accent-color: var(--color-primary);
  flex-shrink: 0;
  margin: 0;
}

.settings-toggle__content {
  flex-direction: column;
  flex: 1;
  gap: .25rem;
  display: flex;
}

.settings-toggle__label {
  color: var(--color-text);
  font-size: 1rem;
  font-weight: 600;
}

.settings-toggle__hint {
  color: var(--color-text-muted);
  margin: 0;
  font-size: .875rem;
}

.settings-label {
  color: var(--color-text);
  margin-bottom: .5rem;
  font-weight: 500;
  display: block;
}

.settings-select {
  border: 2px solid var(--color-border);
  border-radius: var(--radius);
  width: 100%;
  color: var(--color-text);
  cursor: pointer;
  transition: var(--transition);
  background: #fff;
  padding: .75rem;
  font-size: 1rem;
}

.settings-select:hover {
  border-color: var(--color-primary);
}

.settings-select:focus {
  border-color: var(--color-primary);
  outline: none;
  box-shadow: 0 0 0 3px #10b9811a;
}

.settings-info {
  color: var(--color-text-muted);
  padding: var(--space-md);
  background: var(--color-bg);
  border-radius: var(--radius);
  border: 2px solid var(--color-border);
  font-size: .9375rem;
}

.settings-info strong {
  color: var(--color-primary);
  font-size: 1.125rem;
  font-weight: 600;
}

.settings-actions {
  gap: var(--space-sm);
  flex-direction: column;
  display: flex;
}

.settings-btn {
  padding: .75rem var(--space-lg);
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
  text-align: left;
  border: none;
  font-size: 1rem;
  font-weight: 500;
}

.settings-btn--secondary {
  background: var(--color-bg);
  color: var(--color-text);
  border: 2px solid var(--color-border);
}

.settings-btn--secondary:hover {
  border-color: var(--color-primary);
  background: #fff;
}

.settings-btn--danger {
  color: var(--color-terrible);
  background: #ef44441a;
  border: 2px solid #ef44444d;
}

.settings-btn--danger:hover {
  background: var(--color-terrible);
  color: #fff;
  border-color: var(--color-terrible);
}

.settings-text {
  color: var(--color-text-muted);
  margin-bottom: var(--space-md);
  font-size: .9375rem;
  line-height: 1.6;
}

.settings-text strong {
  color: var(--color-text);
}

.settings-list {
  padding-left: var(--space-lg);
  list-style: none;
}

.settings-list li {
  color: var(--color-text-muted);
  font-size: .9375rem;
  line-height: 1.8;
  position: relative;
}

